export default {
	data() {
		return {
			currentIndex: 0,
			list:[
				{img: '../../static/img/img3.png',title: '设计师',money: '15K-20K',adder: '西安-高新',tip: '招聘岗位',company:'阿里巴巴',time: '1-3年工作经验'}
			]
		}
	},
	methods: {
		tab(e){
			this.currentIndex = e
		},
		post(e){
			if(e== 0){
				uni.navigateTo({
					url:'../postCompany/postCompany'
				})
			}else{
				uni.navigateTo({
					url:'../postPersonal/postPersonal'
				})
			}
		},
		go(e){
			if(this.currentIndex == 0){
				uni.navigateTo({
					url:'../companyDetailSkill/companyDetailSkill'
				})
			}else if(this.currentIndex == 1){
				uni.navigateTo({
					url:'../personalDetailSkill/personalDetailSkill'
				})
			}
		}
	}
}
